import { User } from '@joh/libs';
import { BaseEntity } from '@joh/libs/base';
import { Column, Entity, OneToMany, Relation } from 'typeorm';

@Entity()
export class Organization extends BaseEntity {
  @Column({ type: 'varchar' })
  name: string;

  @OneToMany(() => User, user => user.organization)
  users: Relation<User[]>;
}
